.header-color{
  background: #654ea3;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #eaafc8, #654ea3);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #eaafc8, #654ea3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.landing-grid{
  background: #654ea3;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #eaafc8, #654ea3);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #eaafc8, #654ea3); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height:100%;
  text-align: center;
}

.vikas-img{
  height:250px;
  padding-top: 5em;
  border-radius: 50%;
}
.projects-grid{
  display: flex;
}
.banner-text{
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;

}

.banner-text h1{
  font-size: 66px;
  font-weight: bold;
  color: white;
  margin:auto;
}
.banner-text hr{
  border-top: 5px dotted white ;
  width: 50%;
  margin:auto;
}
.banner-text p{
  color: white;
  font-size: 20px;
  padding:1em;
}

.social-links{
  display :flex;
  justify-content: space-between;
  width: 50%;
  margin:auto;

}

.social-links i{
  color: white;
  font-size: 5em;
  padding: 8px;

}

/* contact css*/
.contact-body{
  margin:auto;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid{
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 550px;
  background: white;
  color:black;
  margin-bottom: 20px;
}

.contact-grid h2{
  font-family: 'Anton', sans-serif;
}
.contact-grid p{
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}
.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;

}
.skills_lan{
  color:white;
  background: rgb(20, 20, 20);
  opacity:0.8;
}
.contact-list i{
  font-size: 66px;
  padding-right: 5rem
}

/* contact css*/

.resume-right-col{
  background: #27221F;
  color: white;
  padding: 2em;
}
